<template>
  <v-container fluid>
    <v-layout row wrap>
      <v-flex xs12>
        <h3 class="headline-mobile headline white--text secondary--text">무엇이든 물어보세요</h3>
        <h4 class="caption white--text">Commitment to work or question</h4>
        <div class="contents">
          <v-form ref="form" v-model="valid" lazy-validation>
            <input type="text" class="v-input" placeholder="email">
            <input type="text" class="v-input" placeholder="catagory">
            <textarea type="text" class="v-input" placeholder="contents"></textarea>
            <v-btn color="accent" depressed @click="reset">보내기</v-btn>
            <!--<v-text-field
              v-model="email"
              :rules="emailRules"
              label="E-mail"
              required
              append-icon="close"
            ></v-text-field>

            <v-select
              v-model="select"
              :items="items"
              :rules="[v => !!v || 'Item is required']"
              label="문의 종류"
              required
            ></v-select>

            <v-textarea
              class="textarea"
              name="comment"
              label="내용"
              placeholder="의뢰하실 내용을 적어주세요"
              hint
            ></v-textarea>

            <v-btn :disabled="!valid" color="primary" @click="validate">Validate</v-btn>
            <v-btn color="accent" depressed @click="reset">보내기</v-btn>-->

          </v-form>
        </div>
      </v-flex>
    </v-layout>
  </v-container>
</template>
<script>
import store from "../store";
export default {
  data() {
    return {
      inHeight: this.$store.state.inHeight,
      valid: true,
      name: "",
      nameRules: [
        v => !!v || "Name is required",
        v => (v && v.length <= 10) || "Name must be less than 10 characters"
      ],
      email: "",
      emailRules: [
        v => !!v || "E-mail is required",
        v => /.+@.+/.test(v) || "E-mail must be valid"
      ],
      select: null,
      items: [
        "시스템 개발",
        "시스템 컨설팅",
        "프로그램 제작",
        "ATTO report",
        "in Studio"
      ],
      checkbox: false
    };
  },
  mounted() {
    //console.log(this.$store.state.isMobile)
  },
  methods: {
    validate() {
      if (this.$refs.form.validate()) {
        this.snackbar = true;
      }
    },
    reset() {
      this.$refs.form.reset();
    }
    /* resetValidation() {
      this.$refs.form.resetValidation();
    } */
  }
};
</script>

<style lang="scss" scoped>
.v-menu__content {
  background-color: #fff;
}
.v-messages__wrapper {
  min-height: 20px;
}
.container {
  display: flex;
  height: 100%;
  .layout {
    .flex {
      display: flex;
      flex-direction: column;
      .contents {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        align-items: flex-start;
        height: 100%;
        .v-form {
          width: 100%;
          display: flex;
          flex-direction: column;
          align-items: flex-start;
          height: 100%;
          .v-input {
            width: 100%;
            flex: 0 1 auto;
            padding: 0;
            margin: 0;
            border-bottom: 1px solid #f00;
          }
          textarea.v-input {
            border: 1px solid #f00;
            border-top: 0;
            height: 100%;
          }
        }
      }
    }
  }
}
.v-input__control {
  height: 100% !important;
}
@media (max-width: 750px) {
  .container {
    .layout {
      .flex {
        .contents {
          .v-form {
            align-items: center;
          }
        }
      }
    }
  }
